<template>
	<main class="app">
		<Header v-if="loggedinUser && !isMobile" />
		<MobileHeader v-if="loggedinUser && isMobile" />
		<UserMsg />
		<PwaInstall />
		<router-view />
	</main>
</template>

<script>
import Header from '@/cmps/Header'
import MobileHeader from '@/cmps/MobileHeader'
import UserMsg from '@/cmps/UserMsg'
import PwaInstall from '@/cmps/PwaInstall'
import { getPWADisplayMode } from '@/services/pwa-service.js'

export default {
	components: { Header, UserMsg, MobileHeader, PwaInstall },
	computed: {
		loggedinUser() {
			return this.$store.getters.loggedinUser
		},
		isMobile() {
			return window.innerWidth < 768
		},
	},
}
window.addEventListener('beforeinstallprompt', e => {
	e.preventDefault()
	this.$store.commit('setDeferredPrompt', e)
	showInstallPromotion()
})
window.addEventListener('appinstalled', () => {
	hideInstallPromotion()
	this.$store.commit('setDeferredPrompt', null)
})
if (getPWADisplayMode() !== 'browser') {
	hideInstallPromotion()
}
</script>
